p {
  margin: 0 0 5px;
  font-family: Montserrat, sans-serif;
}

.unvisible {
  display: none;
}

.gallery {
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  align-items: stretch;
  display: grid;
}

.gallery img {
  width: 100%;
  height: 280px;
  border: 1px solid #ccc;
  margin-bottom: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .3);
}

.wrapper {
  background-color: #00b4cc;
}

input {
  width: 75%;
  height: 20px;
  color: #00b4cc;
  border: 3px solid #00b4cc;
  border-radius: 5px;
  outline: none;
  margin-right: 15px;
  padding: 5px;
  font-family: Montserrat, sans-serif;
}

button {
  height: 34px;
  min-width: 100px;
  color: #00b4cc;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  font-family: Montserrat, sans-serif;
}

button:hover, button:focus {
  font-weight: 800;
}

.load-more {
  color: #fff;
  background-color: #00b4cc;
  margin-top: 20px;
}

form {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.photo-card {
  border-top: none;
  padding: 32px auto 32px 16px;
  box-shadow: 2px 2px 6px 2px rgba(18, 173, 216, .745);
}

